import {
	debounce
} from '@/common/utils.js';
export default {
	data() {
		return {
			isPassword: true,
			isPassword1: true,
			checked: true,
			n: true,
			pass: '',
			pwd1: '',
			pwd2: ''
		};
	},
	onBackPress(option) {
		plus.key.hideSoftKeybord();
	},
	computed: {
		allowLogin () {
			return !!(this.pass && this.pwd1 && this.pwd2)
		},
	},
	methods: {
		getNowPwd: function(e) {
			this.pass = e.detail.value;
		},
		getPwd1: function(e) {
			this.pwd1 = e.detail.value;
		},
		getPwd2: function(e) {
			this.pwd2 = e.detail.value;
		},
		checkPwd:function(e){
			this.pwd1 = e.detail.value;
			var str = /^[a-z0-9]{6,16}$/;
			if(!str.test(this.pwd1)) {
				uni.showToast({
					title: '密码格式为6到16位字母或数字',
					icon: 'none',
					duration: 2000
				});
				
			}
		},
		show: function() {
			this.isPassword = !this.isPassword;
		},
		show1: function() {
			this.isPassword1 = !this.isPassword1;
		},
		next: function() {
			var that = this;
			that.n = false;
		},
		back: function() {
			var that = this;
			that.n = true;
		},
		linkToTransfer: debounce(function() {
			uni.navigateTo({
				url: '../login-password/login-password'
			});
		}, 500, true),
		other: function() {
			this.linkToTransfer()
		},
		save: function() {
			var _self = this;
			var str = /^[a-z0-9]{6,16}$/;
			if (this.pass == '') {
				uni.showToast({
					title: '请输入现有密码',
					icon: 'none',
					duration: 2000
				});
				// return false;
			} else if (this.pwd1 == '') {
				uni.showToast({
					title: '请输入登录密码',
					icon: 'none',
					duration: 2000
				});
				// return false;
			} else if (this.pwd1 && this.pwd2 && this.pwd1 !== this.pwd2) {
				uni.showToast({
					title: '两次密码不一致',
					icon: 'none',
					duration: 2000
				});
				// return false;
			} else if (this.pwd2 == '') {
				uni.showToast({
					title: '请确认登录密码',
					icon: 'none',
					duration: 2000
				});
				// return false;
			} else if(!str.test(this.pwd1)) {
				uni.showToast({
					title: '密码格式为6到16位字母或数字',
					icon: 'none',
					duration: 2000
				});
				
			}else{
				uni.request({
					url: this.url + 'updataloginpwd/',
					method: 'POST',
					data: {
						mobile: uni.getStorageSync('phone'),
						password: this.pass,
						password1: this.pwd1,
						password2: this.pwd2
					},
					headers: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success: res => {
						if (res.statusCode == 200) {
							
							uni.removeStorageSync('phone');
							uni.removeStorageSync('token');
							uni.removeStorageSync('nowtime');
							uni.reLaunch({
								url: '../../pages/login/login'
							});
							uni.showToast({
								title: '登录密码修改成功',
								icon: 'none',
								duration: 1500
							});
						}
						if (res.statusCode == 400) {
							uni.showToast({
								title: '现有密码错误',
								icon: 'none'
							});
						}
					}
				});
			}
		
		}
	}
};
